import React, { Component } from 'react';
import AdjustableUI from './AdjustableUI';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
            <Switch>
                <Route path="/">
                    <AdjustableUI />
                </Route>
            </Switch>
        </Router>
      </div>
    );
  }
}

export default App;



